<template>
  <div class="app" @click="onClick">
    <div class="header">
      <img
        v-if="app.icon_uri && !iconFailedToLoad"
        class="icon"
        :src="app.icon_uri"
        @error="iconFailedToLoad = true"
      />
      <div v-else class="icon icon_stub"></div>

      <div class="info">
        <div class="name line">
          <span>
            <button class="filterable ellipsis" @click="updateFilters('name')">{{ app.name }}</button>,
          </span>
          <button class="filterable ellipsis" @click="updateFilters('developer_name')">
            {{ app.developer_name }}
          </button>
        </div>

        <div v-if="app.lead" class="desktop line ellipsis">{{ app.lead }}</div>

        <div>v. {{ app.version }}</div>

        <!--<div v-if="app.is_compatibility_unknown" class="line">{{ $t('COMPATIBILITY_UNKNOWN_1') }}</div>
        <i18next
          v-else-if="compatibleOperatingSystems.length"
          tag="div"
          path="FOR_X"
          class="line ellipsis"
        >
          <span>
            <span
              v-for="os in compatibleOperatingSystems"
              class="compatible-device"
              :key="os.name"
            >
              <button class="filterable" @click="updateFilters({ compatibility: os.name })">{{ os.device }}</button>
            </span>
          </span>
        </i18next>-->
      </div>
    </div>

    <div v-if="app.lead" class="lead mobile">{{ app.lead }}</div>

    <div class="details">
      <div class="line">
        {{ $t('SOURCE') }}:
        <button class="filterable ellipsis" @click="updateFilters('source_name')">{{
            app.source_name
          }}
        </button>
      </div>
      <div class="line">
        <button class="filterable ellipsis" @click="updateFilters('type')">
          {{ $t(app.type?.toUpperCase()) }}
        </button>
      </div>
      <div v-if="app.genre_id" class="line">
        <button class="filterable ellipsis" @click="updateFilters('genre_id')">{{ genre }}</button>
      </div>
    </div>

    <div v-if="$scopedSlots['custom-action']" class="actions">
      <slot name="custom-action" :app="app"/>
    </div>
  </div>
</template>

<script>
import { mapState } from 'vuex';
import { operatingSystems } from '@/data';
import installApp from '@/mixins/installApp';

export default {
  mixins: [installApp],
  props: {
    app: Object,
    shareable: Boolean,
  },
  data() {
    return {
      iconFailedToLoad: false,
      isInstalling: false,
    };
  },
  computed: {
    ...mapState(['genres']),
    genre() {
      return Object.values(this.genres.official)
        .find(genre => genre.id === this.app.genre_id)?.name;
    },
    compatibleOperatingSystems() {
      return operatingSystems.filter(({ name }) => this.app[`min_${name}_version`]);
    },
  },
  methods: {
    updateFilters(filter) {
      if (typeof filter === 'string') {
        this.$emit('update-filters', { [filter]: this.app[filter] });
        return;
      }

      this.$emit('update-filters', filter);
    },
    onClick(e) {
      if (e.target.closest('button')) {
        return;
      }

      this.$router.push(`/details/${this.app.universal_object_identifier}`);
    },
    async install() {
      if (this.app.no_installation_ticket_failure_reason) {
        this.$notify({
          type: 'error',
          text: this.app.no_installation_ticket_failure_reason.translated,
        });
        return;
      }

      this.isInstalling = true;
      try {
        await this.installApp(this.app.object.id, 'universal', { installationTicket: this.app.installation_ticket });
      } finally {
        this.isInstalling = false;
      }
    },
  },
};
</script>

<style lang="scss" scoped>
@import "@/assets/variables";

.line {
  display: flex;
  gap: 0.3em;
  white-space: nowrap;
  padding-bottom: 2px;
  max-width: 100%;
}

.ellipsis {
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}

.app {
  display: flex;
  gap: 15px;
  padding: 10px;
  border: 1px solid var(--tertiary-accent);
  border-radius: 5px;
  cursor: pointer;

  @include tablet-container(search) {
    flex-direction: column;
    gap: 10px;
  }
}

.icon {
  flex: 0 0 57px;
  height: 57px;
  width: 57px;
  border-radius: 22.5%;
  mask: url(../../assets/ios-mask.svg) center/100% 100% no-repeat;
  object-fit: cover;
  object-position: center;

  &_stub {
    background-color: var(--stub-bg);
  }
}

.header,
.info,
.details {
  min-width: 0;
}

.header {
  display: flex;
  gap: 15px;
  flex-grow: 1;
}

.name {
  font-size: 16px;
}

.mobile {
  display: none;

  @include tablet-container(search) {
    display: block;
  }
}


.desktop {
  @include tablet-container(search) {
    display: none;
  }
}

.info {
  display: flex;
  flex-direction: column;
  gap: 3px;
}

.compatible-device {
  &:not(:last-child):after {
    content: ', '
  }
}

.details {
  display: flex;
  flex-direction: column;
  align-items: flex-end;
  gap: 5px;
  margin-left: auto;

  @include tablet-container(search) {
    margin-left: 0;
    align-items: flex-start;
  }
}

.actions {
  display: flex;
  flex-direction: column;
  gap: 5px;
  align-items: stretch;
  white-space: nowrap;
}

.filterable {
  font-size: inherit;
  line-height: inherit;
  color: currentColor;
  text-decoration: underline dotted;
}
</style>
